.modal {
	position: absolute;
	visibility: hidden;
	width: 100vw;
	height: 100vh;
	background-color: black;
	opacity: 0.7;
	z-index: 10000;
	

	.modal-message {
		position: absolute;
		top: 50%;
		left: 50%;
		background-color: white;
		opacity: 1;
		width: 50%;
		border-radius: 15px;
		transform: translate(-50%, -50%);

		p{
			margin: 0;
			padding: 10%;
			color: black;
			font-family: 'Triforce';
		}
	}
}